:root {
  --scale: 8;
  --primary-color: #3b82f6;
  --primary-hover: #2563eb;
  --secondary-color: #f3f4f6;
  --text-color: #1f2937;
  --border-color: #d1d5db;
  --shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

body {
  color: var(--text-color);
  background-color: #f9fafb;
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

h3 {
  color: var(--text-color);
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.25rem;
  font-weight: 600;
}

.container {
  box-shadow: var(--shadow);
  background: #fff;
  border-radius: 16px;
  max-width: 600px;
  margin: 30px auto;
  padding: 24px;
}

#canvasDrawing {
  border: 2px solid var(--border-color);
  width: calc(28px * var(--scale));
  height: calc(28px * var(--scale));
  touch-action: none;
  box-shadow: var(--shadow);
  background: #fff;
  border-radius: 12px;
  margin: 30px auto;
  display: block;
}

.prediction-box {
  background-color: var(--secondary-color);
  border-radius: 12px;
  margin-bottom: 20px;
  padding: 20px;
}

.guess-result {
  text-align: center;
  margin-bottom: 20px;
}

#guessText {
  color: var(--primary-color);
  margin: 10px 0;
  font-size: 48px;
  font-weight: bold;
  display: block;
}

#chartDiv {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
}

#canvasGraph {
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.chart-label {
  text-align: center;
  color: #6b7280;
  margin-top: 10px;
  font-size: .9rem;
}

.training-box {
  background-color: var(--secondary-color);
  border-radius: 12px;
  padding: 20px;
}

.input-group {
  margin-bottom: 15px;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  margin: 5px;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 500;
  transition: all .2s;
}

button:hover {
  background-color: var(--primary-hover);
  transform: translateY(-1px);
}

button:active {
  transform: translateY(0);
}

.train-button {
  width: 100%;
  margin-top: 10px;
}

.center-button {
  margin: 0 auto;
  display: block;
}

.import-button {
  width: auto;
  margin: 5px 0;
}

input[type="number"] {
  border: 1px solid var(--border-color);
  box-sizing: border-box;
  border-radius: 6px;
  width: 100%;
  padding: 10px 12px;
  font-size: .95rem;
}

input[type="number"]:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #3b82f633;
}

label {
  color: var(--text-color);
  margin-bottom: 6px;
  font-weight: 500;
  display: block;
}

#extrasDiv {
  display: none;
}

.button-group {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.button-group-left {
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 10px;
  display: flex;
}

input[type="file"] {
  width: 100%;
  margin-bottom: 10px;
}

@media (width <= 640px) {
  body {
    padding: 10px;
  }

  .container {
    margin: 15px auto;
    padding: 16px;
  }

  #canvasDrawing {
    width: 196px;
    height: 196px;
  }

  :root {
    --scale: 7;
  }

  button {
    width: 100%;
    margin: 5px 0;
  }

  .button-group, .button-group-left {
    flex-direction: column;
  }

  #guessText {
    font-size: 36px;
  }

  .import-button {
    width: 100%;
  }
}
